<!-- 教材首页 -->
<template>
  <div class="i-material" id="Material">
    <div class="i-secondary-menu i-third-menu">
      <i-tabbar
        v-show="!show"
        class="i-small-tabbar"
        title="全部教材"
        num="20"
        unit="个"
        button-text="创建教材"
        link="/teacher/add">
        <div class="i-student-tabbar-pane">
          <i-checkbox-group>
            <i-checkbox
              v-for="i in 2" :key="i"
              :index = "i-1"
              :label="names[i-1]"
              :is-indeterminate="isIndeterminate[i-1]"
              :check-all="checkAll[i-1]"
              :items="cities[i-1]"
              :on-handle-check-all-change="handleCheckAllChange"
              :on-handle-checked-change="handleCheckedChange"
              :on-handle-check-all-clear="handleCheckAllClear"
              @onIndeterminateChange="onIndeterminateChange"
              @onCheckAllChange="onCheckAllChange"
            ></i-checkbox>
          </i-checkbox-group>
        </div>
      </i-tabbar>
      <i-secondary style="border-top: 1px solid #c6cad1" title="约翰汤普森钢琴基础教程" :show-search="false" v-show="show"></i-secondary>
    </div>
    <div class="i-content">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import ITabbar from '../../../components/tabbar'
  import ICheckboxGroup from '../../../components/checkboxgroup'
  import ICheckbox from '../../../components/checkbox'
  import { checkboxMixins } from '../../../mixins/checkbox'
  import ISecondary from '../../../components/secondary'

  import options from '../../../mock/options'

  const name = ['教材等级：', '教材类型：']

  export default {
    components: {
      ITabbar,
      ICheckboxGroup,
      ICheckbox,
      ISecondary
    },
    mixins: [checkboxMixins],
    computed: {
      show () {
        return this.$route.path === '/resource/edit'
      }
    },
    data () {
      return {
        keyword: '',
        checkAll: [false, false, false, false],
        isIndeterminate: [true, true, true, true],
        cities: options.options,
        names: name
      }
    }
  }
</script>
<style lang="less">
  @import "../../../assets/v2/base";

  .i-material {
    padding-top: 25px;
    .i-material-pane {
      max-width: @--main-width;
      margin: 0 auto 40px;
    }
  }
</style>



